// 
const APP_CONFIG = {
  appName: 'V2S',
  // ,
  // true
  isStatic: false,
  // isStatic
  needGetUserInfoFirst: false,
  // session ms, isStatic
  sessionDuration: 30 * 60 * 1000,
  // , isStatic
  httpHeaders: {
    'Content-Type': 'application/json'
  },
  // , true10isStatic
  defaultAuth: false,
  // , , isStatic
  indexPageName: 'indexHome',
  // , isStatic
  loginPageName: 'userLogin',
  // Api, isStatic
  apiPath: {
    default: '/api',
    easybao: {
      development: '/jcy-api',
      test: 'http://test.jcyapi.easybao.com/jcy-api',
      pre: 'http://pre.jcyapi.easybao.com/jcy-api',
      production: 'http://jcyapi.easybao.com/jcy-api'
    }
  },
  //keyapiPath
  proxyTable: {
    '/api': {
      // mocktarget
      target: 'http://localhost:10083',
      // target: 'http://admintest.activity.easybao.com',
      changeOrigin: true,
      pathRewrite: {
        // '^/api': '/'
      }
    },
    '/jcy-api': {
      target: 'http://localhost:10083',
      // target: 'http://test.jcyapi.easybao.com',
      changeOrigin: true,
      pathRewrite: {
        // '^/api': '/'
      }
    }
  },
  // 
  preRenderRouters: [
    // '/login'
  ],
  // e.g  console.log(process.env.SOMETHING) => 
  envs: {
    development: {
      NODE_ENV: '"development"',
      SOMETHING: '""'
    },
    test: {
      NODE_ENV: '"test"'
    },
    pre: {
      NODE_ENV: '"pre"'
    },
    production: {
      NODE_ENV: '"production"'
    }
  },
  // req
  requestInterceptor(req) {
    return req
  },
  // response
  responseInterceptor(res) {
    // http
    // store/utils.jsapicatch
    return res.data
  },
  // 
  errorHandler(e) {
    console.error('' + e)
  }
}

module.exports = APP_CONFIG



