import {doAction} from '../util'
import api from 'api'
// state
const state = {
  key: 'value'
}
// mutations
const mutations = {}
// actions
const actions = {
  getTest({commit}, params) {
    return doAction({
      api: api.getTest,
      params
    })
  }
}
export default {
  state,
  mutations,
  actions
}


