import 'babel-polyfill'
import Vue from 'vue'
import VueRouter from 'vue-router'
import utils from 'utils'
import api from 'api'
import config from 'config'
import App from 'src/App'
import store from './store'
import routes from './router'
import components from 'constants/global'
import directive from 'directives'
import mixins from 'mixins'
import progress from 'nprogress'
import 'nprogress/nprogress.css'

// 
Vue.config.productionTip = false
Vue.config.errorHandler = config.errorHandler || new Function()

progress.configure({
  showSpinner: false
})
Vue.use(VueRouter)

Object.keys(components).forEach(key => {
  let comp = components[key]
  comp = comp.default ? comp.default : comp
  Vue.component(comp.name, comp)
})


/**--------------------------**/
//
const loginTimeout = () => {
  return utils.getLoginRemainingTime() <= 0
}

//
const hasPermission = (permissions, routeName) => {
  permissions = permissions || []
  return permissions.indexOf(routeName) !== -1
}

//store
const rebuildStore = (accessToken, userInfo) => {
  //store
  if (accessToken) {
    if (!loginTimeout()) {
      store.commit('LOGIN', userInfo)
    } else {
      utils.removeUserInfoFromCache()
      store.commit('LOGOUT')
    }
  }
}

const checkPermission = (to, from, next) => {
  let meta = to.meta || {}
  let auth = config.defaultAuth || false
  if (meta.auth !== undefined) {
    auth = meta.auth
  }
  let accessToken = utils.getAccessToken()
  let permissions = store.getters.userInfo.permissions || []
  if (auth) {
    //1.
    if (!accessToken || loginTimeout()) {
      //2.
      next({name: config.loginPageName})
    } else {
      //2.
      if (to.name === config.loginPageName) {
        //3.
        if (hasPermission(permissions, config.indexPageName)) {
          //4.
          next({name: config.indexPageName})
        } else {
          //4.
          next()
        }
      } else {
        //3.
        if (hasPermission(permissions, to.name)) {
          //4.
          next()
        } else {
          //4.401
          console.warn('' + to.name + ' ')
          next({name: '401'})
        }
      }
    }
  } else {
    // 1.
    if (to.name === config.loginPageName) {
      // 2.
      if (loginTimeout()) {
        // 3.
        next()
      } else {
        // 3.
        if (hasPermission(permissions, config.indexPageName)) {
          // 4.
          next({name: config.indexPageName})
        } else {
          // 4.
          next()
        }
      }
    } else {
      // 2.
      next()
    }
  }
}

/*  */

// 1.
const router = new VueRouter({
  mode: 'history',
  linkActiveClass: 'is-active',
  routes,
  scrollBehavior(to, from, savedPosition){
    if (savedPosition) {
      return savedPosition
    } else {
      let position = {}
      if (to.hash) {
        position.selector = to.hash
      }
      if (to.matched.some(m => m.meta.scrollToTop)) {
        position.x = 0
        position.y = 0
      }
      return position
    }
  }
})

// 2.
const initApp = () => {
  //
  directive.init()
  mixins.init()

  // 
  router.beforeEach((to, from, next) => {
    console.log('' + (to.name || to.path))
    progress.start()
    setTimeout(() => {
      progress.done()
    }, 5000)
    if (config.isStatic) {
      return next()
    }
    checkPermission(to, from, next)
  })

  router.afterEach(() => {
    progress.done();
  });

  let vue = new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: {App}
  })
  vue.appConfig = config
}

// 3.
if (config.isStatic) {
  initApp()
} else {
  if (config.needGetUserInfoFirst) {
    api.getUserInfo().then(res => {
      let accessToken = utils.getAccessToken()
      if (accessToken && res.code === 0 && res.data) {
        rebuildStore(accessToken, res.data)
        initApp()
      } else {
        if (config.defaultAuth) {
          console.warn('')
        }
        initApp()
      }
    }).catch(() => {
      initApp()
    })
  } else {
    initApp()
  }
}





